<template>
  <div class="black-bg" v-if="modal == true">
    <div class="white-bg">
      <img :src="rooms[pressed].image" class="room-img">
      <h4>{{ rooms[pressed].title }}</h4>
      <p>{{rooms[pressed].content}}</p>
      <input v-model="week">
      <p>{{week}} weeks selected: ${{rooms[pressed].price * week}}</p>
      <button @click="$emit('closeModal')" class="close-button">Close</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'detailModal',
  data() {
    return {
      week : ''
    }
  },
  watch : {
    week(a){
      if (isNaN(a) == true){
        alert('Please enter numbers only');
      }
    },
  },
  props: {
    rooms : Array,
    pressed: Number,
    modal: Boolean
  }
}
</script>

