<template>
  <div>
    <img :src="room.image" class="room-img">
    <button @click="send" class="card-button">{{ room.title }}      
    </button> 
    <p class="price">${{ room.price }}</p>
  </div>
</template>

<script>
export default {
    name: 'CardView', 
    props : {
        room : Object,    
    },
    methods : {
      send(){
        this.$emit('openModal', this.room.id)
      }
    }

}
</script>

